@charset "UTF-8";
/***
    The new CSS reset - version 1.8.2 (last updated 23.12.2022)
    GitHub page: https://github.com/elad2412/the-new-css-reset
***/
/*
    Remove all the styles of the "User-Agent-Stylesheet", except for the 'display' property
    - The "symbol *" part is to solve Firefox SVG sprite bug
 */
*:where(:not(html, iframe, canvas, img, svg, video, audio):not(svg *, symbol *)) {
  all: unset;
  display: revert;
}

/* Preferred box-sizing value */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Reapply the pointer cursor for anchor tags */
a,
button {
  cursor: revert;
}

/* Remove list styles (bullets/numbers) */
ol,
ul,
menu {
  list-style: none;
}

/* For images to not be able to exceed their container */
img {
  max-inline-size: 100%;
  max-block-size: 100%;
}

/* removes spacing between cells in tables */
table {
  border-collapse: collapse;
}

/* Safari - solving issue when using user-select:none on the <body> text input doesn't working */
input,
textarea {
  -webkit-user-select: auto;
}

/* revert the 'white-space' property for textarea elements on Safari */
textarea {
  white-space: revert;
}

/* minimum style to allow to style meter element */
meter {
  -webkit-appearance: revert;
  appearance: revert;
}

/* preformatted text - use only for this feature */
pre {
  all: revert;
}

/* reset default text opacity of input placeholder */
::placeholder {
  color: unset;
}

/* remove default dot (•) sign */
::marker {
  content: "";
}

/* fix the feature of 'hidden' attribute.
   display:revert; revert to element instead of attribute */
:where([hidden]) {
  display: none;
}

/* revert for bug in Chromium browsers
   - fix for the content editable attribute will work properly.
   - webkit-user-select: auto; added for Safari in case of using user-select:none on wrapper element*/
:where([contenteditable]:not([contenteditable=false])) {
  -moz-user-modify: read-write;
  -webkit-user-modify: read-write;
  overflow-wrap: break-word;
  -webkit-line-break: after-white-space;
  -webkit-user-select: auto;
}

/* apply back the draggable feature - exist only in Chromium and Safari */
:where([draggable=true]) {
  -webkit-user-drag: element;
}

/* Revert Modal native behavior */
:where(dialog:modal) {
  all: revert;
}

body {
  color: #333333;
  font-family: "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, "ＭＳ Ｐゴシック", sans-serif;
  position: relative;
}

.pc {
  display: block;
}
@media screen and (max-width: 768px) {
  .pc {
    display: none;
  }
}

.sp {
  display: none;
}
@media screen and (max-width: 768px) {
  .sp {
    display: block;
  }
}

#headerArea {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  padding: 35px clamp(1.25rem, 1.0378rem + 0.9302vw, 1.875rem);
  width: 100vw;
  top: 0;
  left: 0;
  transition: 0.7s;
  z-index: 1000;
}
#headerArea.scroll {
  padding-top: 21px;
  padding-bottom: 21px;
  background-color: #21054d;
}

#menuBtn {
  display: none;
}
@media screen and (max-width: 768px) {
  #menuBtn {
    display: block;
    position: relative;
    width: 34px;
    height: 34px;
    z-index: 1000;
  }
  #menuBtn span, #menuBtn::before, #menuBtn::after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #fff;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    transition: 0.3s;
  }
  #menuBtn::before {
    transform: translateY(calc(-50% - 5px));
  }
  #menuBtn::after {
    transform: translateY(calc(-50% + 5px));
  }
  #menuBtn.act span {
    opacity: 0;
  }
  #menuBtn.act::before {
    transform: translateY(-50%) rotate(45deg);
  }
  #menuBtn.act::after {
    transform: translateY(-50%) rotate(-45deg);
  }
}

@media screen and (max-width: 768px) {
  #headerNavArea {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    background: transparent linear-gradient(155deg, #642390 0%, #19074a 100%) 0% 0% no-repeat;
    opacity: 0;
    transition: opacity 0.3s;
    visibility: hidden;
  }
}
#headerNavArea.act {
  opacity: 1;
  visibility: visible;
}
#headerNavArea .navList {
  display: flex;
  align-items: center;
  gap: 25px;
  font-family: "Montserrat", sans-serif;
  color: #fff;
  font-weight: 700;
}
@media screen and (max-width: 768px) {
  #headerNavArea .navList {
    flex-direction: column;
    justify-content: center;
    height: 100%;
  }
}

#contactArea {
  padding: clamp(3.75rem, 3.3256rem + 1.8605vw, 5rem) 0;
  background-color: #f7f7f7;
}
#contactArea .titleMain {
  margin: 0 0 clamp(1.875rem, 1.7689rem + 0.4651vw, 2.1875rem);
}
#contactArea .description {
  margin: 0 0 30px;
  font-size: clamp(0.875rem, 0.8538rem + 0.093vw, 0.9375rem);
  line-height: 1.7;
  text-align: center;
}
#contactArea .buttonMain {
  margin: 0 auto;
}

#footerArea {
  padding: 60px 0;
  background-color: #120e19;
}
#footerArea .navArea {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin: 0 0 20px;
  padding: 0 10%;
  font-size: clamp(0.75rem, 0.7076rem + 0.186vw, 0.875rem);
  font-weight: 700;
  color: #fff;
}
@media screen and (max-width: 768px) {
  #footerArea .navArea {
    flex-wrap: wrap;
    justify-content: flex-start;
  }
}

#copyright {
  font-size: 10px;
  letter-spacing: 0.05em;
  color: #a798b7;
  text-align: center;
}

#logo {
  width: clamp(7.75rem, 6.9648rem + 3.4419vw, 10.0625rem);
}

.titleMain {
  color: #391e88;
}
.titleMain h2 {
  margin: 0 0 5px;
  font-family: "Montserrat", sans-serif;
  font-size: clamp(1.875rem, 1.7901rem + 0.3721vw, 2.125rem);
  font-weight: 700;
  letter-spacing: 0.05em;
  line-height: 1.5;
  text-align: center;
}
.titleMain span {
  display: block;
  font-size: clamp(0.8125rem, 0.7913rem + 0.093vw, 0.875rem);
  text-align: center;
}

.buttonMain {
  display: block;
  position: relative;
  padding: 20px 0;
  border-radius: 50px;
  width: 200px;
  background: transparent linear-gradient(90deg, #7633a4 0%, #250d65 100%) 0% 0% no-repeat padding-box;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.05em;
  line-height: 1;
  color: #fff;
  text-align: center;
}
.buttonMain span {
  position: relative;
}
.buttonMain::before {
  content: "";
  display: block;
  position: absolute;
  border-radius: 50px;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background: transparent linear-gradient(90deg, #250d65 0%, #7633a4 100%) 0% 0% no-repeat padding-box;
  transition: 0.5s;
}
.buttonMain::after {
  content: "";
  display: block;
  position: absolute;
  border-style: solid;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  border-left: 10px solid #fff;
  border-right: 0;
  width: 0;
  height: 0;
  top: 50%;
  right: 1.5em;
  transform: translateY(-50%);
  transition: 0.3s;
}
.buttonMain:hover::before {
  opacity: 0;
}
.buttonMain:hover::after {
  right: 1.3em;
}

main {
  height: 1800px;
}